
@font-face {
  font-family: 'Choko';
  src: local('Choko'),
    url('./fonts/Choko.otf') format('truetype');
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'),
    url('./fonts/Montserrat.ttf') format('truetype');
}

@font-face {
  font-family: 'Montserrat-Black';
  src: local('Montserrat-Black'),
    url('./fonts/Montserrat-Black.ttf') format('truetype');
}

html{
  background: linear-gradient(#b15adc, #31caab);
  width: 100%;
  min-width: 360px;
  margin-left: auto;
  margin-right: auto;

}

.header, .roadmap, .tokenomics, .prefooter-conteiner, .footer, .airdrop{
  position: relative;
  z-index: 1;
}

.noise{
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

*{
  margin: 0;
  padding: 0;
}

.wrapper{
  width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.wrapper_{
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

ul{
  list-style-type:  none;  
}
/*header*/

header{
  display: flex;
}


.header-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header-container span{
  font-weight: 700;
}

.header-container-title{
  width: 75%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
}

.header-container-title img{
  width: 762px;
  margin-left: auto;
  margin-right: auto;
  transform:translate(0%, 43%);
  padding-left: 6%;
}

.header-container-content{
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.dogwifcash, .dog{
  display: flex;
}

.dogwifcash{
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.dogwifcash{
  position: absolute;
  left: 15vw;
  top: 26vw;
  flex-direction: column;
}

.dogwifcash h1{
  font-size: 1.7vw;
  padding-bottom: 1.5vw;
  color: #212121;
  font-family: 'Montserrat';
  font-weight: 400;
}

.copy h2{
  font-size: 1.4vw;
  color: #212121;
  font-family: 'Montserrat';
  font-weight: 700;
  padding-top: 1vw;
  padding-left: 2vw;
}

.copy{
  display: flex;
}

.copy input{
  font-size: 1.4vw;
  color: #212121;
  font-family: 'Montserrat';
  font-weight: 700;
  padding-left: 0.3vw;
  padding-top: 1vw;
  background-color: transparent;
  outline:none;
  border: none;
  width: 37vw;
}

.copy button{
  background-color: transparent;
  border: none;
  width: 4vw;
  transform: translate(0px, 0.5vw);;
}


.copy img{
  cursor: pointer;
  width: 4vw;
  padding-left: 2vw;
}

.copy img:hover{
  cursor: pointer;
  width: 4.1vw;
}

.button-conteiner-buy{
  padding-top: 1.5vw;
}

img.dog-left{
  z-index: -1;
  width: 48.594vw;
  padding-top: 6vw;
  padding-bottom: 1vw;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  filter: drop-shadow(0 -3vw 8vw #361ea4);
}

.twenty-conteiner{
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
}


img.dog-right{
  position: relative;
  width: 28.677vw;
  margin-left: auto;
  margin-right: auto;
  z-index: 2;
  filter: drop-shadow(0 -2vw 8vw #361ea4);
}

.social{
  display: flex;
  flex-direction: column;
}

.social-conteiner{
  display: flex;
}

.social-conteiner h2{
  color: #fff;
  text-align: center;
  font-size: 100px;
  margin-bottom: 62.5%;
  
}

.button-conteiner{
  
}

.button  img{
  width: 14.896vw;
}

.wallet-conteiner{
  position: absolute;
  top: 18vw;
  right: 7vw;
  display: flex;
  padding-top: 7.5vw;
}

.wallet{
 flex-direction: column;
}

.wallet h2{
  color: #212121;
  font-family: 'Montserrat';
  padding-bottom: 2vw;
  font-size: 1.7vw;
  font-weight: 400;
}

.wallet span{
  font-family: 'Montserrat-Black';
}

.wallet-transaction{
  display: flex;
  padding: 0.5em 0;
}

.wallet-transaction h3{
  color: #fff;
  font-size: 14.22px;
  padding-top: 0.5vw;
}

.you-pay{
  padding-right: 0.2em;
}

.wallet-transaction input{
  margin: 0 1em;
  padding: 0.5em 0;
}

.social ul{
  display: flex;
}

.social img{
  width: 2.865vw;
}

.social-last-item{
  padding-left: 1.5vw;
}

.social p{
  font-family: 'Choko';
  padding-top: 1.3vw;
  font-size: 1.8vw;
  font-weight: 700;
  color: #212121;
}



.content-cont-dog{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: 3vw;
}

.content-img-dog img{
  width: 29.84vw;
  filter: drop-shadow(0 -3vw 8vw #361ea4);
}

.content-dog p{
  color: #212121;
  font-family: 'Montserrat';
  padding-bottom: 2vw;
  font-size: 1.7vw;
  font-weight: 400;
  padding-top: 1.5vw;
  padding-left: 2vw;
  letter-spacing: 0.1vw;
}

.content-dog span{
  font-family: 'Montserrat-Black';
}


.tokenomics-container {
  padding-top: 10vw;
  position: relative;
}

.tokenomics-container img{
  position: relative;
  z-index: 0;
  width: 93.177vw;
  padding-bottom: 2vw;
}

.tokenomics-container-content h2{
  font-family: 'Choko';
  padding-top: 1.3vw;
  font-size: 6.771vw;
  font-weight: 400;
  color: #79BBE5;
  position: absolute;
  top: 14vw;
  left: 27vw;
}

.tokenomics-content{
  color: #004262;
  font-family: 'Montserrat';
  padding-bottom: 2vw;
  font-size: 1.7vw;
  font-weight: 700;
  position: absolute;
  top: 27.5vw;
  left: 9.5vw;
  text-transform: uppercase;
  filter: drop-shadow(0 0.05vw 0.06vw #004262);
}

.tokenomics-content li{
  padding-bottom: 1vw;
}



.roadmap-container{
  width: 100%;
}

.roadmap-container img{
  display: flex;
  width: 60.156vw;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15vw;
  filter: drop-shadow(0 -4vw 8vw #361ea4);
}


.roadmap-container-content{
  width: 100%;
  position: relative;
}

.roadmap-container-content h2{
  text-align: center;
  font-family: 'Choko';
  padding-top: 1.3vw;
  font-size: 6.771vw;
  font-weight: 400;
  color: #89D8FF;
  padding-top: 4vw;
}

.roadmap-content-item-one, .roadmap-content-item-two, .roadmap-content-item-three, .roadmap-content-item-fore, .roadmap-content-item-five{
  position: absolute;
  color: #DFF3FF;
  font-family: 'Montserrat';
  font-size: 1.667vw;
  font-weight: 400;
}

.roadmap-content-item-one p, .roadmap-content-item-two p, .roadmap-content-item-three p, .roadmap-content-item-fore p, .roadmap-content-item-five p{
  padding-top: 1.5vw;
}

.roadmap-content span{
  font-family: 'Choko';
  font-size: 3vw;
  font-weight: 400;
  color: #DFF3FF;
}

.roadmap-content-item-one{
  top: 16vw;
  left: 1.5vw;
}

.roadmap-content-item-two{
  top: 27vw;
  right:-7vw;
  text-align: right;
}

.roadmap-content-item-three{
  top: 41vw;
  left: 28vw;
}

.roadmap-content-item-fore{
  top: 79vw;
  left: 1.5vw;
}

.roadmap-content-item-five{
  top: 72vw;
  right: -1vw;
}


.airdrop-container-content, .airdrop-content{
  width: 100%;
  position: relative;
}

.airdrop-content img{
  margin-left: auto;
  margin-right: auto;
  display: flex;
  z-index: 0;
  width: 93.177vw;
  padding-top: 15vw;
}

.airdrop-content-item{
  position: absolute;
  top: 27vw;
  right: 9vw;
}

.airdrop-content-item p{
  font-size: 1.875vw;
  color: #004262;
  font-family: 'Montserrat';
  padding-bottom: 2vw;
  font-size: 1.7vw;
  font-weight: 700;
  text-transform: uppercase;
  padding-top: 2.5vw;
  letter-spacing: 0.12vw;
}

.airdrop-content-item span{
  font-family: 'Choko';
  padding-top: 1.3vw;
  font-size: 5.3vw;
  font-weight: 400;
  color: #0080BF;
  text-transform: uppercase;
}

.prefooter{
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}


.prefooter p{
  text-align: center;
  text-transform: uppercase;
  font-family: 'Choko';
  font-size: 2.344vw;
  color: #DFF3FF;
  padding-top: 9vw;
}

.footer{
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}


.footer p{
  text-align: center;
  text-transform: uppercase;
  font-family: 'Choko';
  font-size: 4.167vw;
  color: #CCD000;
  padding-top: 9vw;
  padding-bottom: 6vw;
}


@media  (max-width: 600px){
  .social img{
    width: 4.5vw;
  }

  .copy h2{
    padding-top: 1.59vw;
    font-size: 2vw;
  }

  .copy input{
    font-size: 2vw;
    width: 53vw;
  }

  .copy img{
    width: 6vw;
    transform:translate(0%, 0.5vw);
  }
  
}

@media  (max-width: 321px){
  .airdrop-content img{
    margin-right: 0;
  }
}